body {
  background: #09738a;
  color: white;
  font-size: 40px;
  text-align: center;
}

body #container {
  display: flex;
  height: 100vh;
  margin-left: auto;
  margin-right: auto;
  justify-content: center;
  align-items: center;
}

.App {
  display: block;
}

#title {
  font-size: 50px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-weight: 900;
  letter-spacing: 2px;
}

.label {
  display: inline-block;
  margin: 20px;
}

#container .timer {
  border: 7px solid #78a890;
  margin: 10px auto -20px auto;
  background-color: #e7d9b4;
  color: #09738a;
  width: 18rem;
  height: 160px;
  border-radius: 50px;
  position: relative;
  box-shadow: inset 3px 3px 3px rgba(0, 0, 0, 0.4);
}

#container .timer .timer-wrapper {
  position: absolute;
  height: 110px;
  top: 50%;
  margin-left: auto;
  margin-right: auto;
  margin-top: -67px;
}

#container .timer #time-left {
  font-family: Digital Numbers Regular;
  font-size: 1.5em;
  margin-left: 25px;
}

#timer-label {
  margin-left: 25px;
  margin-bottom: 5px;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

.author {
  text-align: center;
  font-family: Share Tech Mono, sans;
  margin-top: -18px;
  font-size: 14px;
  color: #a50d0d;
}

.author a {
  text-decoration: none;
  color: black;
  line-height: 26px;
}

#up {
  background: none;
  border: none;
  outline: none;
  cursor: pointer;
  margin: 10px;
}

#down {
  background: none;
  border: none;
  outline: none;
  cursor: pointer;
  margin: 10px;
}

.red-text {
  color: rgb(165, 13, 13);
}

#start_stop {
  background: none;
  border: none;
  outline: none;
  cursor: pointer;
  margin: -5px;
}

#reset {
  background: none;
  border: none;
  outline: none;
  cursor: pointer;
  margin: -5px;
}

.timer-control {
  display: flex;
  gap: -5px;
  justify-content: center;
  padding-top: -20px;
}

#start_stop:hover {
  color: #13353a;
}

@font-face {
  font-family: "Digital Numbers Regular";
  font-style: normal;
  font-weight: normal;
  src: local("Digital Numbers Regular"), url("./fonts/DigitalNumbers-Regular.ttf") format("woff");
}
